.intro {
  z-index: 1;
  position: relative;
  //overflow: hidden;

  border-radius: 0 0 20px 20px;

  &--about {
    .intro__content {
      @include vp-1199 {
        display: grid;
        grid-template-columns: 1fr 332px;
        grid-template-rows: min-content min-content 1fr min-content;
        column-gap: 22px;
      }

      @include vp-767 {
        display: flex;
        flex-direction: column;
        column-gap: 0;
      }
    }

    .intro__title {
      text-shadow: none;

      color: $color-nero;

      @include vp-1199 {
        grid-column: 1 / 3;
      }
    }

    .intro__description {
      max-width: 780px;
      margin: 0 0 60px;

      color: $color-nero;

      @include vp-1023 {
        margin: 0 0 50px;
      }

      @include vp-767 {
        max-width: unset;
        margin: 0 0 30px;
      }
    }

    .intro__button {
      width: 220px;

      @include vp-1023 {
        width: 214px;
      }

      @include vp-767 {
        width: 100%;
      }
    }

    .intro__image-wrapper {
      &--tablet {
        @include vp-1199 {
          display: flex;
        }

        @include vp-767 {
          display: none;
        }
      }

      &--mobile {
        @include vp-767 {
          display: flex;
        }
      }
    }
  }

  &__background {
    position: absolute;

    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    border-radius: 0 0 20px 20px;

    &::before {
      content: "";
      position: absolute;

      top: 0;
      left: 0;

      width: 100%;
      height: 100%;

      border-radius: 0 0 20px 20px;
      background: linear-gradient(182deg, rgba($color-sherpa-blue, 0.79) 3.31%, rgba($color-mosque, 0.35) 98.41%);
    }

    img {
      width: 100%;
      height: 100%;

      border-radius: 0 0 20px 20px;

      object-fit: cover;
    }
  }

  &__wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;

    padding: 230px 0 70px;

    @include vp-1023 {
      padding: 192px 0 70px;
    }

    @include vp-767 {
      padding: 120px 0 50px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__title {
    display: flex;
    align-items: baseline;
    max-width: 941px;
    margin: 0 0 40px;

    font-weight: 900;
    font-size: 65px;
    line-height: 72px;
    text-shadow: 0 4px 44px rgba($color-default-black, 0.42);

    color: $color-default-white;

    @include vp-1023 {
      max-width: 688px;
      margin: 0 0 30px;

      font-size: 45px;
      line-height: 50px;
    }

    @include vp-767 {
      max-width: 500px;
      margin: 0 0 20px;

      font-size: 30px;
      line-height: 39px;
    }

    &--medium {
      margin: 0 0 30px;

      font-size: 60px;
      line-height: 66px;

      @include vp-1023 {
        font-size: 50px;
        line-height: 55px;
      }

      @include vp-767 {
        margin: 0 0 20px;

        font-size: 30px;
        line-height: 39px;
      }
    }

    svg {
      width: 31px;
      height: 28px;
      flex-shrink: 0;
      margin-left: 20px;

      @include vp-767 {
        width: 22px;
        height: 20px;
        margin-left: 12px;
      }
    }
  }

  &__tour-count {
    margin-bottom: 20px;

    @include vp-1023 {
      margin-bottom: 40px;
    }

    @include vp-767 {
      margin-bottom: 50px;
    }
  }

  &__description {
    max-width: 612px;
    margin: 0 0 100px;

    font-weight: 500;
    font-size: 20px;
    line-height: 30px;

    color: $color-default-white;

    @include vp-1023 {
      max-width: 515px;
      margin: 0 0 70px;

      font-size: 16px;
      line-height: 24px;
    }

    @include vp-767 {
      margin: 0 0 40px;

      font-size: 15px;
      line-height: 23px;
    }

    &--medium {
      margin: 0 0 50px;

      font-size: 18px;
      line-height: 27px;

      @include vp-1023 {
        margin: 0 0 50px;
      }

      @include vp-767 {
        margin: 0 0 30px;

        font-size: 15px;
        line-height: 23px;
      }
    }
  }

  &__price {
    align-self: flex-start;

    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 0 80px;
    padding: 10px 29px;

    font-weight: 700;
    font-size: 20px;
    line-height: 22px;


    border-radius: 5px;
    background-color: $color-eastern-blue;
    color: $color-default-white;

    @include vp-1023 {
      margin: 0 0 60px;
    }

    @include vp-767 {
      margin: 0 0 40px;
      padding: 9px 18px;

      font-size: 15px;
      line-height: 17px;
    }
  }

  &__button {
    width: 449px;

    @include vp-1023 {
      width: 334px;
    }

    @include vp-767 {
      width: 100%;
    }
  }

  &__image-wrapper {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;

    width: 445px;
    height: 445px;
    margin-right: 34px;
    margin-left: 34px;

    border-radius: 10px;

    @include vp-1439 {
      margin-right: 60px;
    }

    @include vp-1199 {
      grid-column: 2 / 3;
      grid-row: 2 / 5;

      display: none;

      width: 308px;
      height: 460px;
      margin-top: 13px;
      margin-left: 0;
    }

    @include vp-767 {
      width: 280px;
      height: 280px;
      margin-top: 0;
      margin-bottom: 20px;
    }

    &::after {
      content: "";
      position: absolute;

      bottom: 44px;
      left: 44px;

      width: calc(100% - 10px);
      height: calc(100% - 10px);

      border: 1px solid $color-las-palmas;
      border-radius: 10px;

      @include vp-1199 {
        bottom: 30px;
        left: 30px;

        width: calc(100% - 6px);
        height: calc(100% - 6px);
      }

      @include vp-767 {
        bottom: 26px;
        left: 26px;

        width: calc(100% - 6px);
        height: calc(100% - 6px);
      }
    }

    &--tablet {
      display: none;

      @include vp-1439 {
        margin-right: 24px;
      }
    }

    &--mobile {
      display: none;

      @include vp-767 {
        margin-right: 20px;
      }
    }

    img {
      z-index: 1;
      position: relative;

      width: 445px;
      height: 445px;

      border-radius: 10px;

      object-fit: contain;

      @include vp-1199 {
        width: 308px;
        height: 460px;
      }

      @include vp-767 {
        width: 280px;
        height: 280px;
      }
    }
  }

  &__list {
    z-index: 1;
    position: absolute;
    display: flex;
    flex-direction: column;

    right: -66px;
    bottom: 60px;

    margin: 0;
    padding: 0;

    list-style: none;

    @include vp-1439 {
      right: -33px;
    }

    @include vp-1199 {
      right: 0;
    }

    @include vp-767 {
      display: none;
    }
  }

  &__item {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;

    border-radius: 5px;
    background-color: $color-blue-lagoon;
    backdrop-filter: blur(4.5px);

    transition: background-color $trans-default;

    svg {
      flex-shrink: 0;

      width: 51px;
      height: 51px;
    }

    @include hover-focus {
      background-color: $color-teal;
    }
  }
}
